export default {
  name: 'invoice',
  data () {
    return {
      invoiceData: {
        invoiceId: 0,
        companyName: '',
        typeKey: '',
        typeValue: '',
        notice: '',
        amount: '',
        payment: '',
        dateTime: '',
        imageUrl: '',
        pdfUrl: '',
        merContact: ''
      }
    }
  },
  mounted () {
    this.invoiceData.invoiceId = this.$route.params.invoiceId
    this.getInvoiceDetail()
  },
  methods: {
    getInvoiceDetail () {
      this.$vux.loading.show({text: ''})
      setTimeout(() => {
        this.$vux.loading.hide()
        let data = {
          companyName: '深圳家乐福有限公司',
          typeKey: 'success',
          typeValue: '开票处理中',
          notice: '开票处理中提示信息',
          amount: '￥235',
          payment: '上海易通金服有限公司',
          dateTime: '2018.04.05',
          imageUrl: '../../src/assets/img/invoice.png',
          pdfUrl: '',
          merContact: '8765678765'
        }
        switch (this.invoiceData.invoiceId) {
          case '1':
            data = {
              companyName: '深圳家乐福有限公司',
              typeKey: 'success',
              typeValue: '开票处理中',
              notice: '开票处理中提示信息',
              amount: '￥235',
              payment: '上海易通金服有限公司',
              dateTime: '2018.04.05',
              imageUrl: '../../src/assets/img/invoice.png',
              pdfUrl: '',
              merContact: '8765678765'
            }
            break
          case '2':
            data = {
              companyName: '深圳家乐福有限公司',
              typeKey: 'pending',
              typeValue: '开票处理中',
              notice: '开票处理中提示信息',
              amount: '￥235',
              payment: '上海易通金服有限公司',
              dateTime: '2018.04.05',
              imageUrl: '../../src/assets/img/invoice.png',
              pdfUrl: '',
              merContact: '8765678765'
            }
            break
          case '3':
            data = {
              companyName: '深圳家乐福有限公司',
              typeKey: 'fail',
              typeValue: '开票处理中',
              notice: '开票处理中提示信息',
              amount: '￥235',
              payment: '上海易通金服有限公司',
              dateTime: '2018.04.05',
              imageUrl: '../../src/assets/img/invoice.png',
              pdfUrl: '',
              merContact: '8765678765'
            }
            break
          default:
            break
        }

        this.invoiceData = Object.assign({}, this.invoiceData, data)
      }, 2000)

      // this.$http.post(this.API.invoice, {}).then(res => {
      //   this.loading = false
      //   this.invoiceData = res.invoiceData
      // })
    },
    share () {
      console.log('share...')
    },
    download () {
      console.log('downloading...')
    },
    saveFile (data, filename) {
      const saveLink = document.createElementNS('http://www.w3.org/1999/xhtml', 'a')
      saveLink.href = data
      saveLink.download = filename

      const event = document.createEvent('MouseEvents')
      event.initMouseEvent('click', true, false, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null)
      saveLink.dispatchEvent(event)
    }
  }
}



